.header{
  background-color: rgb(42,47,50);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
  > h1{
    color: rgb(236,236,238);
  }
  >h3{
    color: rgb(92,92,94);
    margin-top: 5px;
    margin-bottom: 20px;
  }
  @media screen and (max-width: 720px){
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    > h1{
      margin-top: 20px;
      color: rgb(236,236,238);
    }
    >h2{
      color: rgb(92,92,94);
    }
  }
}